/* Universal Reset and Base Styling */
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  font-family: Arial, sans-serif;
  color: #333;
  background-color: #f9fafb;
  line-height: 1.6; 
}

.hero-image {
  width: 100%;
  height: 400px; 
  border-radius: 8px;
  margin-bottom: 1rem;
  background-image: url('./assets/hero-image.jpg');
  background-size: cover;
  background-position: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

.hero-content {
  color: #ffffff; 
  text-align: center;
  font-size: larger;  
}

/* Colors and Constants */
:root {
  --primary-color: #3474ff;
  --secondary-color: #567ff1;
  --background-color: #ffffff;
  --text-color: #333;
  --hover-shadow: rgba(0, 0, 0, 0.15) 0px 4px 12px;
}

/* Navigation Bar */
.nav {
  background-color: var(--primary-color);
  color: #ffffff;
  padding: 1rem 2rem;
}

.nav-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.nav-brand {
  display: flex;
  align-items: center;
  font-size: 1.5rem;
  font-weight: bold;
  cursor: pointer;
}

.nav-brand img {
  height: 24px;
  margin-right: 0.5rem;
}

.nav-links {
  display: flex;
  gap: 1rem;
}

.nav-button {
  background: transparent;
  color: #ffffff;
  border: none;
  font-size: 1rem;
  cursor: pointer;
  padding: 0.5rem;
  transition: all 0.3s;
}

.nav-button:hover {
  color: #cbd5e1;
  text-shadow: 0 0 8px rgba(255, 255, 255, 0.7);
}

.nav-brand {
  display: flex;
  align-items: center;
  gap: 0.5rem; 
  font-size: 1.5rem;
  font-weight: bold;
  color: #ffffff;
  cursor: pointer;
  border: none;
  background: none;
}

.nav-brand svg {
  fill: #ffffff; 
}
/* Container */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem 1rem;
}

/* Card Styling */
.card {
  background-color: #ffffff;
  padding: 2rem;
  border-radius: 8px;
  box-shadow: var(--hover-shadow);
  transition: transform 0.3s, box-shadow 0.3s;
  margin-bottom: 2rem; 
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: rgba(0, 0, 0, 0.2) 0px 10px 20px;
}

.card-title {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  color: var(--primary-color);
}

.card-description {
  font-size: 1.1rem;
  margin-bottom: 1rem;
}

/* Form Styles */
.form-container {
  max-width: 600px;
  margin: 0 auto;
}

.form-group {
  margin-bottom: 1.5rem; /* Add spacing between form fields */
}

.form-label {
  display: block;
  font-weight: bold;
  color: var(--primary-color);
  margin-bottom: 0.5rem;
}

.form-input,
.form-textarea {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #ddd;
  border-radius: 0.25rem;
  background-color: #f9fafb;
  margin-bottom: 1rem;
  transition: border-color 0.3s;
}

/* Release pet form error styles */
.input-error {
  border-color: red;
}

.textarea-error {
  border-color: red;
}

.error-text {
  color: red;
  font-size: 0.875rem;
  margin-top: 0.25rem;
}

/* Dropdown menu for user */
.user-status {
  position: relative;
  display: flex;
  align-items: center;
  gap: 10px;
}

.dropdown-toggle {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 1.2rem;
  color: #ffffff;
  margin-left: 10px;
}

.dropdown-menu {
  position: absolute;
  top: 40px;
  right: 0;
  background-color: #ffffff;
  border: 1px solid #ddd;
  border-radius: 4px;
  box-shadow: var(--hover-shadow);
  padding: 8px 0;
  z-index: 1000;
  min-width: 150px;
}

.dropdown-item {
  padding: 10px 20px;
  font-size: 1rem;
  color: var(--text-color);
  background: none;
  border: none;
  text-align: left;
  width: 100%;
  cursor: pointer;
}

.dropdown-item:hover {
  background-color: var(--primary-color);
  color: #ffffff;
}

.form-input:focus,
.form-textarea:focus {
  border-color: var(--primary-color);
  outline: none;
}

.button {
  background-color: var(--primary-color);
  color: #ffffff;
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 0.25rem;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s;
  margin-top: 1rem; /* Space above the button */
}

.button:hover {
  background-color: var(--secondary-color);
}

/* Pet Card */
.pet-card {
  background-color: #ffffff;
  border-radius: 8px;
  overflow: hidden;
  padding: 1.5rem;
  transition: all 0.3s;
  box-shadow: var(--hover-shadow);
  margin-bottom: 1.5rem; /* Space between pet cards */
}

.pet-image {
  width: 300px;
  height: 200px;
  object-fit: cover;
  border-radius: 8px; 
}

.pet-card:hover {
  transform: translateY(-5px);
  box-shadow: rgba(0, 0, 0, 0.15) 0px 10px 20px;
}

.pet-image {
  width: 50%;
  height: 50%;
  display: block;
  border-radius: 4px;
  margin-bottom: 1rem; /* Space between image and text */
}

.pet-name {
  font-size: 1.2rem;
  color: var(--primary-color);
  margin-bottom: 0.5rem;
}

.pet-details {
  font-size: 1rem;
  color: #555;
  margin-bottom: 0.5rem;
}

/* Footer */
.footer {
  background-color: var(--primary-color);
  color: #ffffff;
  text-align: center;
  padding: 1.5rem 0;
  font-size: 0.9rem;
  margin-top: 2rem;
}

.footer .social-icons {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-top: 0.5rem;
}

.footer .social-icons a {
  color: #ffffff;
  font-size: 1.5rem;
  transition: color 0.3s;
}

.footer .social-icons a:hover {
  color: #cbd5e1;
}

/* Responsive styles */
@media (max-width: 768px) {
  .nav-links {
    display: none;
  }

  .container {
    padding: 1rem;
  }

  .pet-grid {
    grid-template-columns: 1fr;
  }
}
